  aspect-ratio: 1;\n}\n.container {\n  max-width: 500px;\n  margin: 0 auto;\n  padding: 0 30px 100px 30px;\n  position: relative;\n  min-height: 100vh;\n  display: none;\n}\n\n.home-container {\n  padding-top: 50px;\n}\n\n.search-container {\n  max-width: 90%;\n  display: flex;\n  margin: 0;\n  height: 45px;\n  position: relative;\n}\n\n.search {\n  box-sizing: border-box;\n  width: 100%;\n  border-radius: 30px;\n  border: none;\n  outline: none;\n  font-size: 1.2rem;\n  background-color: #333;\n  transition: background-color 0.3s ease;\n  padding: 0 50px 0 20px;\n  font-family: 'poppins';\n}\n.search:hover,\n.search:focus,\n.search:active {\n  background-color: white;\n  outline: 2px solid #aaa;\n}\n.search-btn {\n  width: 35px;\n  height: 35px;\n  border: none;\n  background-color: lightsteelblue;\n  border-radius: 50%;\n  position: absolute;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  top: 5px;\n  right: 7px;\n}\n.search-btn svg:hover,\n.search-btn svg:focus {\n  cursor: pointer;\n}\n\n.weather-container {\n  display: grid;\n  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));\n  gap: 20px;\n  margin: 20px 0;\n}\n\n.info-container {\n  display: flex;\n  flex-direction: column;\n  align-items: flex-start;\n  font-family: 'poppins';\n  background-color: #ddd;\n  border-radius: 22px;\n  padding: 10px 30px;\n}\n\n.place {\n  margin-top: 40px;\n  font-size: 1.6rem;\n  font-weight: 600;\n  display: flex;\n  align-items: center;\n  justify-content: flex-start;\n  gap: 4px;\n  color: #eee;\n}\n\n.temp {\n  font-size: 3.5rem;\n  font-weight: 600;\n}\n.temp-feel {\n  font-size: 1.2rem;\n  font-family: 'Libertinus+Math';\n  font-weight: 600;\n}\n\n.cloud-condition {\n  font-size: 1.3rem;\n  align-self: flex-end;\n}\n.weather-info {\n  margin: 0 auto;\n  width: 100%;\n  padding: 40px 0;\n  border-radius: 22px;\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  background-color: #149cf9;\n}\n.weather-info > d